import Vue from 'vue'
import App from './App.vue'
import router from './router'
import VueLazyload from 'vue-lazyload'
import VueAnalytics from 'vue-analytics'

Vue.config.productionTip = false

Vue.use(VueLazyload)
Vue.use(VueAnalytics, {
	id: 'UA-115909876-1'
})

new Vue({
	router,
	render: h => h(App),
}).$mount('#app')
